.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;

  @include max-width($tablet) {
    max-width: 480px;
  }

  @include min-width($mobile) {
    width: 480px;
  }

  @include min-width($tablet) {
    width: 768px;
    padding: 0 32px;
  }

  @include min-width($desktop) {
    width: 1440px;
  }
}
